*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  min-height: 100vh;
  background: linear-gradient(to bottom, rgb(4, 7, 46), rgb(13, 16, 63));
}

.language-flag{
  position: absolute;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #fff;
  right: 3%;
  top: 35px;
  color: #fff;
  text-decoration: none;
  font-size: 15px;
  transition: all 0.4s;
}

.language-flag:hover{
  background: rgba(0, 0, 0, 0.5);
}

.language-flag img{
  width: 20px;
}
